.App {
  text-align: center;
  width: 100%;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-link {
  color: #09d3ac;
}
@import url('https://fonts.googleapis.com/css?family=Roboto:300,400');
body {
  position: relative;
}
html, body, #root, .App {
  height:100%;
  margin: 0;
}
h1, h2, h3, h4, h5, h6, p, a {
  font-family: 'Roboto', sans-serif;
}
.homeback {
  text-decoration: none;
  color: white;
  position: absolute;
  font-size: 1.4em;
  top: 20px;
  left: 20px;
}
.loading {
  width: 100%;
  height: 100%;
  position: relative;
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:center;
  background-color: #2a2a72;
  background-image: linear-gradient(315deg, #2a2a72 0%, #009ffd 74%);
}
.loading h2 {
  color: white;
  font-weight: 300;
  font-size: 2em;
}
.clear-day {
  background: rgb(115,186,225);
  background: linear-gradient(17deg, rgba(115,186,225,1) 0%, rgba(127,201,242,1) 59%, rgba(138,219,250,1) 69%, rgba(255,251,188,1) 100%);
}
.clear-night {
  background: rgb(69,166,209);
  background: linear-gradient(17deg, rgba(69,166,209,1) 0%, rgba(7,29,51,1) 0%, rgba(30,79,96,1) 49%, rgba(76,147,162,1) 72%, rgba(171,221,232,1) 85%, rgba(255,255,255,1) 100%);
}
.partly-cloudy-day  {
  background: rgb(93,186,218);
  background: linear-gradient(0deg, rgba(93,186,218,1) 0%, rgba(108,209,233,1) 20%, rgba(105,203,237,1) 32%, rgba(134,212,244,1) 46%, rgba(112,203,232,1) 53%, rgba(95,205,237,1) 63%, rgba(130,229,255,1) 74%, rgba(103,215,242,1) 80%, rgba(106,222,247,1) 89%, rgba(73,218,247,1) 100%);
}
.partly-cloudy-night {
  background: rgb(29,43,84);
  background: linear-gradient(0deg, rgba(29,43,84,1) 0%, rgba(109,154,187,1) 48%, rgba(114,167,203,1) 55%, rgba(113,172,209,1) 61%, rgba(138,190,218,1) 70%, rgba(137,196,224,1) 79%, rgba(128,185,209,1) 86%, rgba(80,155,186,1) 100%);
}
.cloudy {
  background: rgb(169,189,198);
  background: linear-gradient(0deg, rgba(169,189,198,1) 0%, rgba(224,227,230,1) 20%, rgba(234,236,236,1) 36%, rgba(224,231,233,1) 48%, rgba(228,234,236,1) 54%, rgba(228,233,233,1) 62%, rgba(222,226,227,1) 69%, rgba(230,235,235,1) 76%, rgba(234,236,236,1) 84%, rgba(217,221,223,1) 91%, rgba(233,239,240,1) 100%);
}
.rain {
  background: rgb(207,217,223);
  background: linear-gradient(90deg, rgba(207,217,223,1) 0%, rgba(201,212,219,1) 5%, rgba(209,219,225,1) 11%, rgba(211,221,227,1) 19%, rgba(213,223,228,1) 27%, rgba(205,220,228,1) 32%, rgba(215,225,230,1) 36%, rgba(217,227,232,1) 45%, rgba(211,219,223,1) 51%, rgba(219,229,234,1) 57%, rgba(214,223,228,1) 61%, rgba(221,231,236,1) 65%, rgba(223,232,237,1) 73%, rgba(212,226,233,1) 77%, rgba(224,233,238,1) 82%, rgba(225,234,239,1) 88%, rgba(217,229,236,1) 92%, rgba(225,234,239,1) 96%, rgba(226,235,240,1) 100%);
}
.snow {
  background-color: #DCD9D4; 
  background: linear-gradient(to bottom, rgba(255,255,255,0.50) 0%, rgba(0,0,0,0.50) 100%), radial-gradient(at 50% 0%, rgba(255,255,255,0.10) 0%, rgba(0,0,0,0.50) 50%), url("./images/snow.png") no-repeat;
  background-blend-mode: soft-light,screen;
  background-size: cover;
}
.sleet {
  background: rgb(183,189,193);
  background: linear-gradient(72deg, rgba(183,189,193,1) 0%, rgba(171,179,187,1) 6%, rgba(180,192,199,1) 11%, rgba(163,173,181,1) 19%, rgba(182,189,194,1) 27%, rgba(171,185,190,1) 32%, rgba(180,194,201,1) 36%, rgba(177,186,191,1) 45%, rgba(180,193,199,1) 51%, rgba(170,181,186,1) 57%, rgba(172,186,193,1) 61%, rgba(186,198,204,1) 65%, rgba(191,200,204,1) 73%, rgba(183,192,196,1) 77%, rgba(179,191,198,1) 82%, rgba(186,195,200,1) 88%, rgba(199,208,214,1) 92%, rgba(195,205,210,1) 96%, rgba(208,218,223,1) 100%);
}
.wind {
  background: rgb(149,208,230);
  background: linear-gradient(90deg, rgba(149,208,230,1) 13%, rgba(182,229,244,1) 21%, rgba(182,229,244,1) 43%, rgba(210,241,255,1) 50%, rgba(210,245,255,1) 80%, rgba(201,241,245,1) 86%, rgba(212,252,252,1) 100%);
}
.fog {
  background-image: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
}
.hail {
  background: linear-gradient(-180deg, #BCC5CE 0%, #929EAD 98%), radial-gradient(at top left, rgba(255,255,255,0.30) 0%, rgba(0,0,0,0.30) 100%);
  background-blend-mode: screen;
}
.thunderstorm {
  background: url("./images/thunderstorm.jpg") no-repeat;
  background-position: center;
  background-size: cover;
}
.tornado {
  background: url("./images/tornado.jpg") no-repeat;
  background-position: center;
  background-size: cover;
}
.restaurantbkgd {
  background-image: linear-gradient(to top, #48c6ef 0%, #6f86d6 100%);
}
.wrapper {
  width: 100%;
  /* height: 100%; */
  position: relative;
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:center;
  background-repeat: no-repeat;
    background-size: cover;
  background-position: center center;
  padding: 40px 0;
}
.wrapper2 {
  width: 100%;
  height: 100%;
  position: relative;
  display:flex;
  flex-direction:column;
  align-items:center;
  background-repeat: no-repeat;
    background-size: cover;
  background-position: center center;
  padding: 40px 0;
}
.container {
  position: relative;
  display: block;
  margin: 0 auto;
  width: 85%;
  min-width: 300px;
  background-color: rgba(255, 255, 255, .15);  
  backdrop-filter: blur(5px);
  border-radius: 3px;
  border: 1px solid rgba(255, 255, 255, .65);
  /*padding: 10px 20px;*/
  box-shadow: 0 8px 10px 1px rgba(0,0,0,.14), 0 3px 14px 2px rgba(0,0,0,.12), 0 5px 5px -3px rgba(0,0,0,.2);
  margin-bottom: 15px;
}
.container2 {
  position: relative;
  display: block;
  margin: 40px auto;
  width: 85%;
  min-width: 300px;
  background-color: rgba(255, 255, 255, .15);  
  /* backdrop-filter: blur(5px); */
  border-radius: 3px;
  border: 1px solid rgba(255, 255, 255, .65);
  /*padding: 10px 20px;*/
  box-shadow: 0 8px 10px 1px rgba(0,0,0,.14), 0 3px 14px 2px rgba(0,0,0,.12), 0 5px 5px -3px rgba(0,0,0,.2);
}
.cuisine-suggestion {
  position: relative;
  display: block;
  margin: 0 auto;
  width: 85%;
  min-width: 300px;
  background-color: rgba(255, 255, 255, .15);  
  backdrop-filter: blur(5px);
  border-radius: 3px;
  border: 1px solid rgba(255, 255, 255, .65);
  padding: 10px 0 20px;
  box-shadow: 0 8px 10px 1px rgba(0,0,0,.14), 0 3px 14px 2px rgba(0,0,0,.12), 0 5px 5px -3px rgba(0,0,0,.2);
}
.header h1 {
  text-align: center;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  margin: 0;
  color: #ffffff;
  padding: 20px 0;
}
.weatherbox {
  text-align: center;
  padding: 20px;
}
.locationbox {
  position: relative;
   display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
  justify-content:         center;
  flex-direction: column;
  /* padding: 20px 20px 0; */
}
.datebox h3 {
  font-weight: 300;
  margin-top: 0;
  color: white;
}
.locactioninfobox {
  /* -webkit-flex-basis: 350px;
  flex-basis:         350px;*/
  /* text-align: left; */
}
.cityarea h2 {
  color: #ffffff;
  font-family: 'Roboto', sans-serif;
  font-weight: normal;
  font-size: 2em;
  margin: 0;
}
.countryarea {
  position: relative;
   display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
  justify-content:         center;
  margin: 0 auto;
  /* text-align: left; */
}
.countryarea h3 {
  margin: 0 0 5px 0;
  font-family: 'Roboto', sans-serif;
  font-weight: normal;
  color: #ffffff;
}
.countryarea h3:first-child {
  margin-right: 8px;
}
.mainline hr {
  border: 0;
  height: 1px;
  background: #ffffff;
  opacity: 0.2;
}
.temperaturearea {
  padding-bottom: 10px;
}
.toptempbox {
  position: relative;
   display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
  justify-content:         center;
}
.tempbox {
  /* -webkit-flex-basis: 180px;
  flex-basis:         180px; */
}
.temperaturearea span#temp {
  position: relative;
  color: #ffffff;
  font-family: 'Roboto', sans-serif;
  font-size: 10em;
}
.temperaturearea #temp:after {
    content: '';
    position: absolute;
    height: 10px;
    width: 10px;
    top: 16px;
    right: -17px;
    border: 3px solid #ffffff;
    border-radius: 50%;
}
.wconditionsbox {
   -webkit-align-self: flex-end;
  align-self: flex-end;
  padding-bottom: 35px;
  padding-left: 5px;
   /* -webkit-flex-basis: 100px;
  flex-basis:         100px; */
}
.wconditionsbox span {
  display: block;
  /* text-align: left; */
}
#wparameter {
  font-size: 3.5em;
}
#wdescription {
  font-size: 1em;
}
#wparameter, #wdescription {
  color: #ffffff;
  font-family: 'Roboto', sans-serif;
}
.weather > span {
  position: relative;
  font-family: 'Roboto', sans-serif;
  font-size: 1.2rem;
  color: #ffffff;
}
.weather > span:before {
    content: '';
    position: absolute;
    left: -10px;
    top: 0px;
    height: 3px;
    width: 3px;
    border: 2px solid #ffffff;
    border-radius: 50%;
}
.main-toggle span {
  margin: 0 0 0 16px;
}
.main-toggle span:last-child {
  margin-left: 11px;
}
.weather button {
		background: #6bbf6b;
		border: none;
		border-radius: 30px;
		outline: none;
		width: 45px;
		height: 20px;
		margin: 5px 5px 0;
		cursor: pointer;
		position: relative;
		transition: background .2s;
}
.weather button:active {
		background: #67b567;
	}
.weather #unit-toggle {
		position: absolute;
		display: inline-block;
		left: -8px;
		top: 2px;
		width: 15px;
		height: 15px;
		background: #fff;
		border-radius: 50%;
		transition: left .2s;
}
#unit-toggle.toggle {
		left: 16px;
}
.cuisine-suggestion h3 span {
  background-color: rgba(255, 255, 255, .45);
  padding: 5px 8px;
  border-radius: 4px;
}
.restaurant-box {
  width: 90%;
  margin: 0 auto;
}
.restaurant-box a {
  text-decoration: none;
  color: white;
}
.card {
  display: flex;
  position: relative;
  width: 100%;
  align-items: center;
  margin-bottom: 10px;
  border: 1px solid rgba(255, 255, 255, .45);
}
.card .restaurantimg {
  width: 80px;
  height: 80px;
  margin-right: 10px;
}
.restaurantimg img {
  width: 100%;
  height: auto;
}
.restaurant-details, .single-restaurant-details {
  text-align: left;
}
.restaurant-details h2, .restaurant-details h3, .restaurant-details h4, .single-restaurant-details h2, .single-restaurant-details h3, .single-restaurant-details h4 {
  margin: 0;
}
.restaurant-details h2 {
  font-size: 1.2em;
}
.restaurant-details span.ratingtext, .single-restaurant-details span.ratingtext {
  font-size: 0.5em;
}
.restaurant-details h3 {
  font-size: 1em;
  font-weight: 300;
}
.restaurant-details h4 {
  font-size: 0.8em;
  font-weight: 300;
}
.star {
  font-size: 1.2em;
}
.stars-outer {
  display: inline-block;
  position: relative;
  font-family: FontAwesome;
}
.stars-outer::before {
  content: "\f006 \f006 \f006 \f006 \f006";
}
.stars-inner {
  position: absolute;
  top: 0;
  left: 0;
  white-space: nowrap;
  overflow: hidden;
  width: 0;
}
.stars-inner::before {
  content: "\f005 \f005 \f005 \f005 \f005";
  /* color: #f8ce0b; */
}
.radius-text {
  margin: 0 0 10px;
  font-weight: 300;
  text-align: left;
}
.restaurantbox {
  width: 100%;
  padding: 20px;
  display: flex;
  box-sizing: border-box;
}
.restaurantbox .restaurantimg {
  width: 200px;
  height: 200px;
  margin-right: 20px;
}
.restaurantbox .single-restaurant-details h2 {
  font-size: 2em;
  margin-bottom: 10px;
  color: white;
}
.restaurantbox .single-restaurant-details h3 {
  font-weight: 300;
  margin-bottom: 10px;
  color: white;
}
.raddress {
  font-size: 1.5em;
}
.restaurantbox .single-restaurant-details h4 {
  font-size: 1.2em;
  font-weight: 300;
  color: white;
}
.single-restaurant-details {
  color: white;
}
.restaurant-labels, .rlinks {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 25px 0;
  flex-wrap: wrap;
}
.rlinks {
  padding-top: 0px;
}
.rbox {
  width: 49%;
  margin-bottom: 20px;
  color: white;
}
.rphone a {
  text-decoration: none;
  font-size: 1em;
  color: white;
}
.rprice h3 span {
  font-size: 1em;
}
.zomatobtn a {
  display: inline-block;
  padding: 10px 12px;
  font-size: 1em;
  color: black;
  background-color: white;
  text-decoration: none;
  border-radius: 5px;
  transition: 1s all;
}
.zomatobtn a:hover {
  background-color: lightgray;
}
.directionbtn button {
  display: inline-block;
  padding: 10px 12px;
  font-size: 1em;
  color: black;
  background-color: white;
  border-radius: 5px;
  border: none;
  transition: 1s all;
  cursor: pointer;
}
.directionbtn button:hover {
  background-color: lightgray;
}
@media screen and (max-width: 479px) {
   .container, .cuisine-suggestion {
  width: 90%;
  min-width: 300px;
}
.restaurant-details h2 {
  font-size: 0.8em;
}
.restaurant-details h3 {
  font-size: 0.6em;
}
.card .restaurantimg {
  width: 70px;
  height: 70px;
}
.restaurant-details span.ratingtext, .restaurant-details h4 {
  display: none;
}
}
@media screen and (min-width: 480px) and (max-width: 900px) {
  .container, .cuisine-suggestion {
  width: 65%;
  min-width: 300px;
  }
}